@import "variables";
@import "talks";
@import "forms";



/* Google font */
@import url(https://fonts.googleapis.com/css?family=Lato:400,300);

body {
  font-family                         : 'Lato', sans-serif;
  font-weight                         : 400;
  color                               : #333333;
}
/* View Helpers */
.line-break {
  margin-bottom                       : 1em;
}
.display-table-cell {
    display: table-cell !important;
}
.icon-item-padding {
    padding-right: 5px
}
.line-break-half {
  margin-bottom                       : 0.5em !important;
}

.line-break-half-top {
  margin-top                       : 0.5em !important;
}

.line-break-dbl {
  margin-bottom                       : 2em;
}
.line-break-large {
  margin-bottom                       : 4em;
}
.line-break-top {
  margin-top                          :1em;
}
.line-break-dbl-top {
  margin-top                          : 2em;
}
.display-block {
  display                             : block;
}

.nopadding {
   padding                              : 0 !important;
   margin                               : 0 !important;
}
.nosidepadding {
    padding-left: 0! important;
    padding-right: 0! important;
}
.noborder {
    border: 0 !important;
}

.noborderradius {
    border-radius: 0 !important;
}

.jumbotron p {
  font-size:17px;
}

/* disable iOS to style text fileds and buttons */
input[type="submit"], input[type="button"],
input[type="text"], textarea, button {
    -webkit-appearance                  : none;
}


/********************************************************************
  NAVIGATION
********************************************************************/

/**************
ADMIN
*****************/
#adminnav> li.active > a {
   background-color                     : none !important;
}
#adminnav {
    padding-top                         : 20px;
}

/* Font in Top Naviation Bar */
.navbar-nav>li>a {
  font-size                             : 1.2em;
}
#username {
  display                               : block;
  padding                               : 15px;
}
/* Top Navigation Bar style overide */
.navbar-custom {
  border-radius                       : 0 !important;
  background-color                    : white;
}
/* To Navigation dropdown menu style overide */
.submenu-icons {
    margin-right                      : 8px;
}
/* Style to "glue" navbar to the top */
.affix {
  top                                 : 0;
  width                               : 100%;
  position                            : fixed;
  z-index                             : 99999;
}
.affix + .container-fluid {
  padding-top                         : 50px;
}


/********************************************************************
  TOP 
********************************************************************/

#top-content {
  height                              : 130px;  
  height                              : 130px;
	background-position                 : center; 
  background-repeat                   : no-repeat;
}
.logo {
  margin-top                          : 15px;
  margin-bottom                       : 15px;
  height                              : 100px;
  
}


/********************************************************************
  MAIN CONTENT
********************************************************************/

#main-content {
  margin-top                          : 50px;
  margin-bottom                       : 150px;
}
/* custom red hyperlink */
.red-link, .red-link:hover, .red-link:active, .red-link:visited {
  color                               : red;
  text-decoration                     : none;
}

.badge-success {
  background-color                    : #5cb85c;
}


/********************************************************************
  CONTACT
********************************************************************/


/********************************************************************
    SIGNUP
********************************************************************/

.signup {
  background-color                    : $light-grey;
  min-height                          : 260px;
  padding-top                         : 30px;
}

.btn-signup:hover, .btn-signup:active {
    background-color: white !important;
    
}
/********************************************************************
    FOOTER 
********************************************************************/

footer {
  background-color                    : #262626;
  min-height                          : 150px;
  color                               : #ccc;
  padding-top                         : 30px;
  padding-bottom                      : 30px; 
}

.footer-credits {
  padding-top                         : 20px;
}

/* hyperlinks in footer */
.footer-color, .footer-color:active, .footer-color:visited, .footer-color:hover, footer-color:link {
    color                               : #ccc;
    text-decoration                     : none;
}

.footer-color:hover {
    color: #fff;
}




